  .Home {
    text-align: center;
    background-color: var(--bg-primary);
    min-height: 90vh;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    color: var(--text-primary);
  }

  .blurb h1{
    margin: 0;
    text-align: left;
    font-size: 50px;
    font-weight: 400;
  }
  .blurb h3{
    margin: 0;
    text-align: left;
    font-size: 30px;
    font-weight: 400;
  }
  
  .headshot {
    height: 300px;
    margin: 20px;
    border: 3px solid var(--action-primary);
    border-radius: 10px;
    box-shadow: 7px 7px 7px var(--bg-darker);
    /* pointer-events: none; */
  }

  @media screen and (max-width: 1000px) {
    .Home {
      flex-direction: column;
      padding-top: 20px;
    }
    .blurb h1{
      text-align: center;
      font-size: 45px;
    }
    .blurb h3{
      text-align: center;
      font-size: 25px;
    }
  }
  
